<template>
<div class="issueListBox">
  <h4 class="lz-title-h4">请确认您的告知异常选项</h4>
  <div v-for="(list,i) in intell.issList">
    <div class="text">{{i+1}}：{{list.diseaseDesc}}</div>
    <div class="question" v-if="list.arrgeMent" v-for="(item,n) in list.arrgeMent">Q{{n+1}}：{{item.diseaseName}}
      <p v-for="(val,s) in item.subDiseaseList" v-if="val.check">A{{n+1}}：{{val.name}}
      </p>
    </div>
  </div>
  <div class="btn">
    <div class="lz-isIndex" @click="resultFun(1)">已完成健康告知</div>
  </div>
    <div class="lz-isIndex" @click="resultFun(2)">还有其他疾病需要反馈</div>
   <div class="lz-isIndex" @click="resultFun(0)">上一步</div>
    <div style="color: red" class="padding-top-30" align="center">注:如有答题需回到上一题,请点击“上一步”</div>
</div>
</template>
<script>
  import Vue from "vue";
  import motheds from "@/api/motheds";
  import { mapMutations } from "vuex";
  import { mapGetters } from "vuex";
  var vm;
  export default {
    name: "Issue",
    data() {
      return {
        resultdata:[],
        diseaseCode:''
      };
    },
    created() {
      vm = this;
      this.getQuery()
    },
    methods: {
      getQuery(){
        this.desLevel = parseInt(this.$route.params.desLevel);
        this.intell.diseseList.splice(this.desLevel+1) //删掉当前等级之后的疾病列表
        this.intell.issList.splice(this.desLevel+1); //删除当前等级之后的问题列表
        this.set_msg({ key: "intelligent", data: this.intell });
      },
      //type 0后退 1确认 2返回疾病列表
      resultFun(type) {
        switch (type) {
          case 0:
            this.$router.go(-1)
            break;
          case 1: 
            vm.$router.push({name: "Result3"});
            break;
          case 2:
            if(this.intell.results.result == 2){
              vm.$router.push({name: "Result3"});
            }else{
              this.$router.push({ path:"/duola/index/0/"+(this.desLevel+1)})
            }
            break;
        }
      },
       ...mapMutations({
        set_msg: "SET_MESSAGE"
      })
    },
    computed: {
      // 使用对象展开运算符将 getter 混入 computed 对象中
      ...mapGetters([
        "intell"
        // ...
      ])
    },
    components: {},
    mounted() {
      console.log(this.issList);
    }
  };
</script>

<style lang="scss">
.issueListBox {
  font-size: 16px;
  background-color: #fff;
  min-height: 100%;
  .btn {
    overflow: hidden;
    margin: 20px 0;
  }
  .text {
    display: inline-block;
    padding: 15px 0 0 15px;
    font-weight: bold;
  }
  .question {
    padding: 15px;
    line-height: 1.5;
    border-bottom: 1px solid #ccc;
  }
}
</style>


